<!--
 * @Description: 角色详情信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-10-09 18:46:59
 -->
<template>
  <div id="switch__">
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="800px">
        <v-card>
          <v-card-title>
            <span class="headline">角色详情</span>
          </v-card-title>
          <v-card-text>
            <p class="root_p">
              角色名称
              <v-chip>{{ data.role_name }}</v-chip>
            </p>
            <p class="root_p">
              管理/代理
              <v-chip>{{ data.level == 1?"管理人员":data.level == 2?"代理人员":"管理人员" }}</v-chip>
            </p>
            <p class="root_p">
              创建时间
              <v-chip>{{ data.create_time | time }}</v-chip>
            </p>
            <p class="root_p">
              上次修改时间
              <v-chip>{{ data.update_time | time }}</v-chip>
            </p>
            <p class="root_p">已获得的权限</p>
            <v-flex md12 sm12>
              <v-chip
                class="text-xs-center"
                v-for="(item,index) in data.root_path"
                :key="index"
              >{{ item.name }}</v-chip>
            </v-flex>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 右上角提示框 -->
    <v-snackbar
      v-model="alert.snackbar"
      :right="true"
      :timeout="alert.timeout"
      :top="true"
      :multi-line="true"
    >
      {{ alert.text }}
      <v-btn color="pink" flat @click="alert.snackbar = false">确认</v-btn>
    </v-snackbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      alert: {
        text: "",
        timeout: 3000,
        snackbar: false
      },
      dialog: false,
      data: {}
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    info(data) {
      this.dialog = data.state;
      this.data = data.list;
    }
  }
};
</script>
<style lang="less">
#switch__ {
  .v-input__slot {
    margin-bottom: 0px !important;
  }
  .v-input--selection-controls {
    margin-top: 0px !important;
  }
  .v-list__tile__content {
    display: block !important;
  }
  .v-list__tile--avatar {
    min-height: 70px;
    display: inline-table;
  }
}
.root_p {
  font-size: 16px;
  padding-left: 17px;
}
</style>
