<!--
 * @Description: 列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-09 18:46:35
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="role_name_" label="角色名称"></v-text-field>
          </v-flex>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field label="手机号码"></v-text-field>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="loader_">搜索</v-btn> -->
          <v-spacer></v-spacer>
          <template v-for="(item,index) in root_path">
            <Module :key="index" v-if="item.url == '/api/v1/manages/add_role'" ></Module>
          </template>
          <Module1 ref="module1" />
          <Module2 ref="module2" />
        </v-layout>
      </v-card-title>
      <!-- 
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts_"
        class="elevation-1"
        :total-items="0"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        :hide-actions="true"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="detailsMessage( props.item )">
          <td>{{ props.index + 1 }}</td>
          <td class="text-xs-centre">{{ props.item.role_name }}</td>
          <td class="text-xs-centre">{{ props.item.level==1?"管理人员":"代理人员" }}</td>
          <td class="text-xs-centre">{{ props.item.create_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.update_time | time }}</td>
          <td class="text-xs-centre">
            <v-chip :color="`teal lighten-4`" class="ml-0" label small> {{ props.item.root_path.length }} 个权限 </v-chip>
            </td>
          <td class="text-xs-centre">

            <v-btn text icon @click="detailsMessage( props.item )" >
              <v-icon color="primary"  >visibility</v-icon>
            </v-btn>
            <template v-for="(item,index) in root_path">
              <v-btn :key="index" v-if="item.url == '/api/v1/manages/edit_role'" text icon  @click="modifyMessage( props.item )" >
                <v-icon color="warning">edit</v-icon>
              </v-btn>
             </template>
            <template v-for="(item,index) in root_path">
              <v-btn :key="index" v-if="item.url == '/api/v1/manages/delete_role'" text icon @click="delMessage_box( props.item.role_id )" >
                <v-icon color="error">clear</v-icon>
              </v-btn>
            </template>


            <!-- <v-btn small color="primary" @click="detailsMessage( props.item )" >角色详情</v-btn>
            <template v-for="(item,index) in root_path">
              <v-btn :key="index" v-if="item.url == '/api/v1/manages/edit_role'" small color="warning" @click="modifyMessage( props.item )" >修改角色</v-btn>
            </template>
            <template v-for="(item,index) in root_path">
              <v-btn :key="index" v-if="item.url == '/api/v1/manages/delete_role'" small color="error" @click="delMessage_box( props.item.role_id )">删除角色</v-btn>
            </template> -->


          </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text" > 没有更多数据了 :( </p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template> -->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>

    <!-- 确认框 -->
      <v-dialog v-model="dialog_logout" max-width="290">
        <v-card>
          <v-card-title class="headline">删除操作</v-card-title>
          <v-card-text> 确认删除该角色吗？（删除角色将会把该角色下的所有管理/代理账号一同删除） </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
            <v-btn color="green darken-1" flat="flat" @click="delMessage">确认删除</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module from "./components/module.vue";
import Module1 from './components/module1.vue'
import Module2 from './components/module2.vue'
export default {
  components: {
    Breadcrumbs,
    Module,
    Module1,
    Module2
  },
  data() {
    return {
      // 筛选字段
      role_name_:"",
      // 确认框
      dialog_logout: false,
      infoData: false,
      
      pagingList: [100, { text: "100", value: 100 }],
      loader: false,
      id: 1,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "角色列表",
          disabled: false,
          to: "/roleList"
        }
      ],
      headers: [
        {
          text: "#",
          align: "left",
          sortable: false, // 是否可排序
          value: "name"
        },
        { text: "角色名称", value: "calories", sortable: false },
        { text: "管理/代理", value: "calories", sortable: false },
        { text: "创建时间", value: "fat", sortable: false },
        { text: "修改时间", value: "carbs", sortable: false },
        { text: "描述", value: "protein", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  computed:{
    // 前端筛选
    desserts_: function(){
      return this.desserts.filter(( blog )=>{
        return blog.role_name.match(this.role_name_);
      })
    }
  },
  methods: {
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    info() {
      this.loader = true;
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_role,
        data: {}
      })
        .then(response => {
          //请求成功返回的数据

          if (response.data.result === 0) {
            this.desserts = response.data.datas;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    detailsMessage( list ){
      let datas = {
        state:true,
        list: list
      }
      this.$refs.module2.info(datas);
    },
    // 修改操作
    modifyMessage( list ){
      let datas = {
        state:true,
        list: list
      }
       this.$refs.module1.info(datas);
    },
    // 删除操作
    delMessage() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_delete_role,
        data: {
          role_id: this.id
        }
      })
        .then(response => {
          //请求成功返回的数据

          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "角色删除成功!",timeout: 3000, snackbar: true });
            this.dialog_logout = false;
            this.id = '';
            this.info();
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 删除确认
    delMessage_box(id){
      this.id = id;
      this.dialog_logout = true;
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>
