<!--
 * @Description: 修改账号信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-29 19:00:25
 -->
<template>
  <div id="switch_">
    <!-- <v-btn color="primary" @click="dialog = true" >+ 创建子账号123 </v-btn> -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="800px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on">+ 创建子账号 {{ dialog }} </v-btn>
        </template>-->

        <v-card>
          <v-card-title>
            <span class="headline">修改角色（权限层级）</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field v-model="role_name" label="角色名称（2-16）" type="text" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-select v-model="level" :items="['管理人员', '代理人员']" label="管理/代理角色" required></v-select>
                </v-flex>
                <v-flex v-for="(item,index) in root_path" :key="index" xs12 sm6 md3 lg3 >
                  <v-switch color="red darken-3" v-model="item.state" :label="item.name"></v-switch>
                </v-flex>
              </v-layout>
            </v-container>
            <small>*敏感的权限请谨慎添加</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="childMethod($event)">提交修改</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,

      role_id:"",
      role_name: "",
      level: "",
      root_path: []
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    info(data) {
        this.role_id = data.list.role_id;
        this.role_name = data.list.role_name;
        this.level = (data.list.level == 1?"管理人员":data.list.level == 2?"代理人员":"管理人员");

       // 只能编辑当前登录账户有的权限
      this.root_path = [];
      this.dialog = data.state;
      let datas = this.$cookie.getLocalStorage("root_path").root_path;
      datas.forEach(item => {
        item.state = false;
        this.root_path.push(item);
      });
      data.list.root_path.forEach(item => {
          this.root_path.forEach(item1 => {
              if( item.name == item1.name ){  
                    item1.state = true;
                    return false;
               }
          })
      });
    },
    childMethod() {
      if (this.role_name.length < 2 || this.role_name.length > 16) {
        this.$Dialog.getProductCheckResult_({text: "请输入正确的角色名称（6-16位）!",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.level == "") {
        this.$Dialog.getProductCheckResult_({text: "请选择一个管理/代理!",timeout: 3000, snackbar: true });
        return false;
      }
      // 过滤用户选中
      let rootList = [];
      this.root_path.forEach(item => {
        if (item.state) {
          rootList.push(item);
        }
      });
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_edit_role,
        data: {
          role_id: this.role_id,
          role_name: this.role_name,
          level: this.level == "管理人员" ? 1 : this.level == "代理人员" ? 2 : 1,
          root_path: rootList
        }
      })
        .then(response => {
          //请求成功返回的数据

          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "修改角色成功!",timeout: 3000, snackbar: true });
            this.role_name = "";
            this.level = "";
            this.dialog = false;
            // 刷新列表
            this.$parent.$parent.info();
          } else {
            // 服务器异常
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less">
// #switch_{
.v-input__slot {
  margin-bottom: 0px !important;
}
.v-input--selection-controls {
  margin-top: 0px !important;
}
// }
</style>
